










































































































import Vue from "vue";
import { Component } from "vue-property-decorator";

import Node from "@/plugins/Node";
import { AppNode, data } from "@/plugins/AppNode";
import { priority } from "@/plugins/TypesInterfaces";

import AppHeader from "@/components/AppHeader.vue";
import AppSubheader from "@/components/AppSubheader.vue";
import AppBody from "@/components/AppBody.vue";
import AppFloatingButton from "@/components/AppFloatingButton.vue";

import ItemsSort from "@/components/ItemsSort.vue";
import ItemsForm from "@/components/ItemsForm.vue";
import ItemsCard from "@/components/ItemsCard.vue";
import ItemCard from "@/components/ItemCard.vue";

import draggable from "vuedraggable";
import { uid } from "uid/secure";

@Component({
  name: "Home",
  components: {
    AppHeader,
    AppSubheader,
    AppBody,
    AppFloatingButton,

    ItemsSort,
    ItemsForm,
    ItemsCard,
    ItemCard,
    draggable,
  },
  watch: {
    root(newRoot: Node) {
      this.$data.level = newRoot.getDepth;
    },
  },
})
export default class App extends Vue {
  /* Data */
  root: AppNode | null = null;
  level = 0; // the current depth level of the tree
  loading = true;
  mode: "view" | "edit" = "view";
  changes: any[] = [];

  /* Hooks */
  async created(): Promise<void> {
    const root = new AppNode("root", { name: "Root" } as data);
    this.root = root;

    this.loading = true;

    this.root = await this.$store.dispatch("buildUserWishlistsTree", {
      uid: this.userId,
    });

    this.loading = false;
  }

  /* Computed */
  // formaters
  getPercentage(percentage: number): string {
    return new Intl.NumberFormat(undefined, {
      style: "percent",
    }).format(percentage);
  }

  getCost(cost: number): string {
    return new Intl.NumberFormat(undefined, {
      style: "currency",
      currency: "USD",
      minimumFractionDigits: 0,
    }).format(cost);
  }

  // getters
  getItemOptinalData(item: AppNode): string {
    let result = "";

    result += `${item.total_done} of ${item.children.length}`;

    if (this.level === 1) {
      result += ` | ${item.priority}`;
      result += ` | ${item.due_date}`;
    }

    if (this.level === 2) result += ` | ${item.priority}`;

    return result;
  }

  get icons(): Record<string, unknown> {
    return this.$vuetify.icons.values;
  }

  get getItemsFormType(): string {
    return this.level === 0
      ? "wishlist"
      : this.level === 1
      ? "item"
      : "subitem";
  }

  get userId(): string {
    return this.$store.state.user.uid;
  }

  /* Methods */
  // travase
  moveForward(itemId: string): void {
    if (this.level <= 1)
      this.root = this.root?.first((node) => node.id === itemId) as AppNode;
  }

  moveBackward(): void {
    if (this.root?.parent) this.root = this.root?.parent as AppNode;
  }

  toggleMode(): void {
    if (this.mode === "view") this.edit();
    else this.done();
  }

  // edit item
  checkItem(itemId: string): void {
    const item = this.root?.first(
      (child: Node) => child.id === itemId
    ) as AppNode;

    if (!item.isLeaf) return;

    item.done = item.done ? 0 : 100;

    console.log(item.done);
  }

  // Save user's changes into the changes array & tree
  operation(type: string, e: any): void {
    if (type === "update") {
      const changeObj = this.changes.find((obj) => obj.item.id == e.id);
      const itemIndex = this.changes.findIndex((obj) => obj.item.id == e.id);

      const node = this.root?.first((node) => node.id === e.id) as AppNode;
      if (node) {
        if (e.name) node.data.name = e.name;
        if (e.cost) node.data.cost = e.cost;
      }

      if (itemIndex === -1) this.changes.push({ type, item: e });
      else if (changeObj.type === "add") {
        changeObj.item.name = e.name;
      } else this.changes.splice(itemIndex, 1, { type, item: e });

      return;
    }

    if (type === "delete") {
      const changeObj = this.changes.find((obj) => obj.item.id == e.id);
      const itemIndex = this.changes.findIndex((obj) => obj.item.id == e.id);

      const node = this.root?.first((node) => node.id === e.id);
      node?.drop();

      if (itemIndex === -1) this.changes.push({ type, item: e });
      else if (changeObj.type === "add") this.changes.splice(itemIndex, 1);
      else this.changes.splice(itemIndex, 1, { type, item: e });

      return;
    }

    if (type === "add") {
      type dataT = {
        name: string;
        priority?: priority;
        due_date?: string;
        user?: string;
        done?: number;
        cost?: number;
      };

      const data: dataT = {
        name: e.name,
        priority: e.priority as priority,
        due_date: e.due_date,
        user: this.userId,
        done: 0,
        cost: Number(e.cost),
      };

      if (this.level === 0) {
        data.cost = 0;
        data.done = 0;
      } else if (this.level === 1) {
        delete data.due_date;
        delete data.user;
      } else if (this.level === 2) {
        delete data.due_date;
        delete data.user;
        delete data.priority;
      }

      const newId = uid();

      this.root?.addChild(new AppNode(newId, data as data));

      this.changes.push({
        type,
        item: {
          ...e,
          id: newId,
          user: this.userId,
        },
      });

      this.done();

      return;
    }
  }

  edit(): void {
    this.mode = "edit";
  }

  done(): void {
    this.mode = "view";

    // create path
    let path: string | AppNode[] | string[] = this.root?.getPath as AppNode[];
    path = path.filter((node: Node) => node.id !== "root");
    path = path.reduce((prev, cur) => prev + "/" + cur.id, "") as string;
    path = "wishlist" + path;

    path = path.split("/");

    if (path.length > 2) {
      path.splice(path.length - 1, 0, "items");
      if (path.length === 5) path.splice(path.length - 3, 0, "items");
    }

    if (path.length === 2 || path.length === 4) path.push("items");

    path = path.join("/");
    // end

    if (this.level === 3) return;

    this.changes.forEach((obj) => {
      if (obj.type === "update") {
        const data: { name: string; cost?: number } = {
          name: obj.item.name,
          cost: Number(obj.item.cost),
        };

        if (this.level === 0) delete data.cost;

        this.$store.dispatch("updateItem", {
          path,
          itemId: obj.item.id,
          data,
        });

        console.log(path, obj, data);
      }

      if (obj.type === "add") {
        type dataT = {
          name: string;
          cost?: number;
          done?: number;
          due_date?: string;
          priority?: string;
          user?: string;
        };

        const data: dataT = {
          name: obj.item.name,
          cost: Number(obj.item.cost),
          done: 0,
          due_date: obj.item.due_date,
          priority: obj.item.priority,
          user: obj.item.user,
        };

        if (this.level === 0) {
          delete data.cost;
          delete data.done;
        } else if (this.level > 0) {
          delete data.user;
          delete data.due_date;
        }

        this.$store.dispatch("addItem", {
          path,
          itemId: obj.item.id,
          data,
        });
      }

      if (obj.type === "delete") {
        this.$store.dispatch("deleteItem", {
          path,
          itemId: obj.item.id,
        });
      }
    });

    this.changes = [];
  }

  save(): void {
    console.log("save item");
  }
}
