































































import { Component, Vue } from "vue-property-decorator";

@Component({
  props: {
    type: {
      type: String,
      default: "wishlist",
    },
    item: {
      type: Object,
      default: null,
    },
    pos: {
      type: String,
      default: undefined,
    },
  },
})
export default class ItemsForm extends Vue {
  //   @Prop() type: "wishlist" | "item" | "subitem" = "wishlist";

  mode: "create" | "edit" = "create";

  localItem = {
    name: "",
    cost: null,
    priority: "normal",
    due_date: "unknown",
  };

  created(): void {
    if (this.$props.item) {
      this.mode = "edit";
      this.localItem = {
        name: this.$props.item.name,
        cost: this.$props.item.cost,
        priority: this.$props.item.priority,
        due_date: this.$props.item.due_date,
      };
    } else {
      this.mode = "create";
    }
  }

  add(): void {
    if (this.localItem.name.length < 1) return;
    if (this.$props.type !== "wishlist" && this.localItem.cost == null) return;

    this.$emit("add-item", this.localItem);

    this.localItem.name = "";

    const el = this.$refs.nameField! as HTMLInputElement;
    el.focus();
  }

  update(): void {
    if (this.mode === "create") return;
    this.$emit("update-item", { id: this.$props.item.id, ...this.localItem });
  }
}
