













import { Component, Vue } from "vue-property-decorator";

@Component
export default class ItemsSort extends Vue {
  sortMethods: Record<string, unknown>[] = [
    // {
    //   name: "alpha",
    //   ascending: true,
    //   icon: function(): string {
    //     return `mdi-sort-alphabetical-${
    //       this.ascending ? "ascending" : "descending"
    //     }-variant`;
    //   },
    // },
    {
      name: "bool",
      ascending: true,
      icon: function(): string {
        return `mdi-sort-bool-${
          this.ascending ? "descending" : "ascending"
        }-variant`;
      },
    },
    {
      name: "numeric",
      ascending: true,
      icon: function(): string {
        return `mdi-sort-numeric-${
          this.ascending ? "descending" : "ascending"
        }`;
      },
    },
    {
      name: "priority",
      ascending: true,
      icon: function(): string {
        return `mdi-sort-${this.ascending ? "descending" : "ascending"}`;
      },
    },
    {
      name: "calendar",
      ascending: true,
      icon: function(): string {
        return `mdi-sort-calendar-${
          this.ascending ? "descending" : "ascending"
        }`;
      },
    },
  ];

  activeMethod: string = this.sortMethods[0].name as string;

  sort: string = this.sortMethods[0].name as string;
  ascending: boolean = this.sortMethods[0].ascending as boolean;

  // Methods
  changeActiveSortMethod(method: Record<string, unknown>): void {
    this.activeMethod = method.name as string;
    method.ascending = !method.ascending;

    this.sortMethods
      .filter((m) => m.name != method.name)
      .forEach((m) => (m.ascending = true));

    this.changeSortMethod(method);
  }

  changeSortMethod(newMethod: Record<string, unknown>): void {
    this.sort = newMethod.name as string;
    this.ascending = newMethod.ascending as boolean;

    let sortName: "name" | "cost" | "status" | "date" | "priority";

    switch (this.sort) {
      case "alpha":
        sortName = "name";
        break;
      case "numeric":
        sortName = "cost";
        break;
      case "bool":
        sortName = "status";
        break;
      case "calendar":
        sortName = "date";
        break;
      default:
        sortName = "name";
    }

    this.doSort(sortName, this.ascending ? "asc" : "desc");
  }

  doSort(
    method: "name" | "cost" | "status" | "date" | "priority",
    type: "asc" | "desc"
  ): void {
    // sort methods' functions
    type Tsort = {
      cost?: (type: string) => (a: any, b: any) => number;
      status?: (type: string) => (a: any, b: any) => number;
      date?: (type: string) => (a: any, b: any) => number;
      name?: (type: string) => (a: any, b: any) => number;
      priority?: (type: string) => (a: any, b: any) => number;
    };

    const sort: Tsort = {};

    sort.name = (type) => {
      return (a, b) => {
        a = a.name.toLowerCase();
        b = b.name.toLowerCase();

        if (a > b) return type === "desc" ? -1 : 1;
        if (a < b) return type === "desc" ? 1 : -1;

        return 0;
      };
    };

    sort.cost = (type) => {
      return (a, b) => {
        a = a.cost ? a.cost : a.total_cost;
        b = b.cost ? b.cost : b.total_cost;
        return type === "desc" ? a - b : b - a;
      };
    };

    sort.status = (type) => {
      return (a, b) => {
        a = a.done ? a.done : a.progress;
        b = b.done ? b.done : b.progress;
        return type === "desc" ? a - b : b - a;
      };
    };

    sort.priority = (type) => {
      return (a, b) => {
        const priorityValue = (priorityString: string) => {
          if (priorityString === "high") return 1;
          if (priorityString === "low") return -1;
          return 0;
        };

        a = priorityValue(a.priority);
        b = priorityValue(b.priority);

        return type === "desc" ? b - a : a - b;
      };
    };

    sort.date = (type) => {
      return (a, b) => {
        a = new Date(Date.parse(a.due_date));
        b = new Date(Date.parse(b.due_date));

        return type === "desc" ? b - a : a - b;
      };
    };

    // Send the appropriate sort functions to the "sort" method
    if (method == "name") this.$emit("sort-method-changed", sort.name(type));
    else if (method == "cost")
      this.$emit("sort-method-changed", sort.cost(type));
    else if (method == "status")
      this.$emit("sort-method-changed", sort.status(type));
    else if (method == "date")
      this.$emit("sort-method-changed", sort.date(type));
    else if (method == "priority")
      this.$emit("sort-method-changed", sort.priority(type));
  }
}
