<template>
  <v-card flat>
    <v-card-text class="pa-0">
      <v-row align="center">
        <v-col cols="2">
          <v-progress-circular
            :value="progress"
            size="30"
            width="4"
            color="primary"
            buffer-value="0"
          />
        </v-col>
        <v-col>
          <div class="text-h5">{{ name }}</div>
          {{ optinalData }}
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="1"><v-icon v-text="icons.cost"/></v-col>
        <v-col>{{ totalCost }}</v-col>
        <v-col cols="1"><v-icon v-text="icons.spent"/></v-col>
        <v-col>{{ totalSpent }}</v-col>
        <v-col cols="1"><v-icon v-text="icons.remain"/></v-col>
        <v-col>{{ remaining }}</v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  props: {
    path: Array || undefined,
    progress: {
      type: [Number, String],
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
    optinalData: {
      type: String,
      required: true,
    },
    totalCost: {
      type: [Number, String],
      required: true,
    },
    totalSpent: {
      type: [Number, String],
      required: true,
    },
    remaining: {
      type: [Number, String],
      required: true,
    },
  },
  computed: {
    icons() {
      return this.$vuetify.icons.values;
    },
    breadcrumbs() {
      if (this.path && this.path.length > 1)
        return this.path
          .filter((node) => node.id !== this.root?.id)
          .map((node) => ({
            text: node.data.name,
          }));
      else return undefined;
    },
  },
};
</script>

<style></style>
